@import url(https://fonts.googleapis.com/css2?family=monsterrat&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Monsterrat', sans-serif;
  background-color: #222222;
  color: #fff;
  font-size: 62.5%;
}

.data-wrapper {
  display: flex;
  flex-direction: column;
  border: 2px solid #e2e2e2;
  border-radius: 5px;
  margin: 0;
  padding: 2rem;
}

.coin-input {
  font-size: 2rem;
  margin: 3rem 0 3rem 0;
  padding-left: 1rem;
  width: 30rem;
  height: 5rem;
  border-radius: 0.5rem;
  border: none;
  background-image: linear-gradient(120deg, #3023ae, #c86dd7);
  color: #e2e2e2;
}

.coin-input::-webkit-input-placeholder {
  color: #e2e2e2;
}

.coin-input:-ms-input-placeholder {
  color: #e2e2e2;
}

.coin-input::placeholder {
  color: #e2e2e2;
}

.btn {
  font-size: 2rem;
  margin-left: 2rem;
  height: 5rem;
  width: 10rem;
  color: #e2e2e2;
  border-radius: 4px;
  border: none;
  background-image: linear-gradient(
    -225deg,
    #ac32e4 0%,
    #7918f2 48%,
    #4801ff 100%
  );
  text-decoration: none;
  background-color: none;
  cursor: pointer;
}
.sbmt {
  background-color: transparent;
}

@media screen and (max-width: 1030px) {
  .coin-app {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 6rem;
    color: #fff;
    overflow: scroll;
  }

  .coin-app .coin-search h1 {
    font-size: 3.5rem;
  }
  .coin-search {
    margin-bottom: 6rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .input-wrapper {
    margin: 2rem 0 2rem 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 6rem;
  }

  .coin-search .input-wrapper .coin-input {
    padding-left: 2rem;
    margin: 0;
    margin-left: 0;
    width: 80vw;
    height: 6rem;
  }

  .coin-search .btn,
  .coin-search .sbmt {
    margin-left: 0;

    width: 80vw;
    height: 6rem;
    font-size: 2rem;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .coin-search .btn {
    margin-bottom: 2rem;
  }
}

@media screen and (min-width: 1030px) {
  .coin-app h1 {
    font-size: 3rem;
  }

  form {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
  }

  .coin-app {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 6rem;
    color: #fff;
    overflow: scroll;
  }

  .coin-search {
    margin-bottom: 6rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .coin-text {
    margin-bottom: 3rem;
    text-align: center;
  }

  .input-wrapper {
    display: flex;
    flex-direction: row;
  }
}

.coin-row {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  padding: 1rem;
  height: 80px;
  width: 90vw;
  border-bottom: 1px solid #d7d7d7;
}

.coin {
  padding: 1rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 50%;
}

.coin h1 {
  display: none;
}

.coin img {
  height: 3rem;
  width: 3rem;
  margin-right: 2rem;
}

.coin-symbol {
  font-size: 2rem;
  text-transform: uppercase;
}

.coin-row .coin-data {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  width: 50%;
}
.coin-price,
.coin-percent {
  font-size: 1.6rem;
  padding: 0.25rem 0 0.25rem 0;
  text-align: right;
}

.coin-volume {
  display: none;
}

.coin-marketcap {
  display: none;
}

.red {
  color: #f00606;
}

.green {
  color: green;
}

@media screen and (min-width: 1030px) {
  .coin-container {
    display: flex;
    justify-content: center;
  }

  .coin-row {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    height: 80px;
    width: 900px;
    border-bottom: 1px solid #d7d7d7;
  }

  .coin {
    display: flex;
    align-items: center;
    padding-right: 24px;
    min-width: 300px;
  }

  .coin-volume,
  .coin-marketcap,
  .coin h1,
  .coin-data {
    display: block;
    font-size: 1.5rem;
  }

  .coin h1 {
    font-size: 16px;
    width: 150px;
  }

  .coin img {
    height: 30px;
    width: 30px;
    margin-right: 10px;
  }

  .coin-symbol {
    text-transform: uppercase;
  }

  .coin-row .coin-data {
    display: flex;
    flex-direction: row;
    text-align: center;
    justify-content: space-between;
    width: 100%;
  }

  .coin-price {
    width: 110px;
  }

  .coin-volume {
    width: 200px;
  }

  .coin-marketcap {
    font-size: 1.5rem;
    width: 240px;
  }

  .coin-percent {
    width: 80px;
  }
}

